  background-color: #ffffff;
}
.main-header {
  display: flex;
  flex-direction: column;
  color: #444444;
  background-color: #ffffff;
  margin: 0 auto;
  z-index: 1;
}
.main-header__logo {
  display: block;
  margin: 22px;
  line-height: 0;
}
.main-header__benefit {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  color: #444444;
}
@media (max-width: 767px) {
  .main-header__wrapper {
    margin: 0 auto;
  }
  .main-header__benefit {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    border: 0;
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
  .main-header__toggle {
    position: absolute;
    right: 0;
    z-index: 2;
    display: block;
    width: 20px;
    height: 20px;
    border: none;
    background-color: transparent;
    cursor: pointer;
    padding: 35px;
  }
  .main-header__toggle--closed::before {
    content: "";
    position: absolute;
    top: 32px;
    left: 20px;
    width: 20px;
    height: 2px;
    background-color: #231f20;
    box-shadow: 0 6px 0 0 #231f20, 0 12px 0 #231f20;
  }
  .main-header__toggle--closed:active::before {
    background-color: rgba(35, 31, 32, 0.6);
    box-shadow: 0 6px 0 0 rgba(35, 31, 32, 0.6), 0 12px 0 0 rgba(35, 31, 32, 0.6);
  }
  .main-header__toggle--opened::before,
  .main-header__toggle--opened::after {
    content: "";
    position: absolute;
    top: 37px;
    left: 16px;
    width: 27px;
    height: 2px;
    background-color: #231f20;
  }
  .main-header__toggle--opened::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .main-header__toggle--opened::after {
    transform: rotate(-45deg);
  }
  .main-header__toggle--opened:active::before,
  .main-header__toggle--opened:active::after {
    background-color: rgba(35, 31, 32, 0.6);
  }
  .main-header__toggle--nojs {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-header {
    position: relative;
    background-color: transparent;
  }
  .main-header__wrapper {
    position: absolute;
    top: 25px;
    left: 48px;
  }
  .main-header__logo {
    margin: 0;
  }
  .main-header__toggle {
    display: none;
  }
  .main-header__benefit {
    padding: 20px 18px;
    margin-bottom: 0;
    margin-left: auto;
    margin-right: 32px;
    margin-top: -64px;
  }
}
@media (min-width: 1150px) {
