.radiobutton__label {
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
  font-size: 17px;
  line-height: 18px;
  color: #000000;
  padding-left: 61px;
  position: relative;
  display: flex;
  min-height: 42px;
  align-items: center;
}
.radiobutton__label::before,
.radiobutton__label::after {
  content: "";
  position: absolute;
  border-radius: 50%;
  box-sizing: border-box;
}
.radiobutton__label::before {
  width: 42px;
  height: 42px;
  background-color: #ffffff;
  border: 2px solid #444444;
  left: 0;
}
.radiobutton__label::after {
  display: none;
  width: 16px;
  height: 16px;
  background-color: #62d1ba;
  left: 13px;
  top: 13px;
}
.radiobutton__label:hover::before {
  border-color: #62d1ba;
}
.radiobutton__label:active::before {
  border-color: rgba(0, 0, 0, 0.3);
}
.radiobutton__input:checked + .radiobutton__label::after {
  display: block;
}
.radiobutton__input:focus-visible ~ .radiobutton__label {
  outline: #000000 auto 2px;
}
@media (min-width: 768px) {
  .radiobutton__label {
    font-size: 19px;
    min-height: 52px;
    padding-left: 78px;
  }
  .radiobutton__label::before {
    width: 52px;
    height: 52px;
  }
  .radiobutton__label::after {
    width: 20px;
    height: 20px;
    left: 16px;
    top: 16px;
  }
}
